import React from 'react';
import { Link } from 'react-router-dom';
import { Button } from 'react-bootstrap';
// import { base_url } from '../config/index.js';

class Dropdown extends React.Component {
    state = { hover: false }

    renderList = (listData) => {
        return (
            <div>
                {listData.map(course => {
                    return (
                        <Link to={`${course.link}`} className="item">
                            {course.name}
                        </Link>
                    );
                })}
            </div>
        );
    }

    render() {
        return (
            <div className="ui dropdown item" onMouseEnter={() => this.setState({ hover: true })} onMouseLeave={() => this.setState({ hover: false })}>
                {this.props.title} <i className="dropdown icon"></i>
                <div className={this.state.hover ? "menu transition visible" : "menu transition"}>
                    {this.renderList(this.props.courses)}
                </div>
            </div>
        )
    }
}

const Header = () => {
    return (
        <div className="ui stackable borderless menu">
            <Link to="/" className="item">
                <img className="ui small image"
                    src={`${process.env.PUBLIC_URL}/assets/images/proctur_logo.png`}
                    alt="logo"
                />
            </Link>
            <div className="right menu">
                <Dropdown title={"Courses"} courses={[{ name: 'Engineering', link: 'engineering' }, { name: 'Medical', link: 'medical' }, { name: 'Banking', link: 'banking' }]} />
                <Dropdown title={"Tutors"} courses={[{ name: 'KCA', link: 'kca' }, { name: 'MEDPG', link: 'medpg' }, { name: 'KAKADE', link: 'kakade' }]} />
                <Dropdown title={"Institutes"} courses={[{ name: 'Allen Career Institute', link: '/institute/allen-career-institute' }, { name: 'Medical', link: '/institute/kca' }, { name: 'Banking', link: '/institute/uwill' }]} />
                <Link to="/login" style={{ display: 'flex', alignItems: 'center', marginRight: 20 }}>
                    <Button bsStyle="item" style={{}}>
                        Login / Sign Up
                    </Button>
                </Link>
            </div>
        </div>
    );
};

export default Header;