import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import "./index.css";

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            menuOpen: false,
        }
    }

    handleMenuClick() {
        this.setState({ menuOpen: !this.state.menuOpen });
    }

    handleLinkClick(value) {
        switch (value) {
            case 'Home':
                if (this.props.homeDiv.current) {
                    this.props.homeDiv.current.scrollIntoView({
                        behavior: "smooth",
                        // block: "nearest"
                    })
                }
                break;
            case 'About':
                // if (this.props.aboutDiv.current) {
                this.props.aboutDiv.current.scrollIntoView({
                    behavior: "smooth",
                    // block: "nearest"
                })
                // }
                break;
            case 'Experience':
                if (this.props.resumeDiv.current) {
                    this.props.resumeDiv.current.scrollIntoView({
                        behavior: "smooth",
                        // block: "nearest"
                    })
                }
                break;
            case 'Portfolio':
                if (this.props.projectsDiv.current) {
                    this.props.projectsDiv.current.scrollIntoView({
                        behavior: "smooth",
                        // block: "nearest"
                    })
                }
                break;
            case 'Skill Set':
                if (this.props.projectsDiv.current) {
                    this.props.projectsDiv.current.scrollIntoView({
                        behavior: "smooth",
                        // block: "nearest"
                    })
                }
                break;
            case 'Contact':
                if (this.props.contactDiv.current) {
                    this.props.contactDiv.current.scrollIntoView({
                        behavior: "smooth",
                        // block: "nearest"
                    })
                }
                break;
        }

        this.setState({ menuOpen: false });
    }

    render() {
        const styles =
        {
            container: {
                position: 'absolute',
                top: 0,
                left: '16vw',
                zIndex: '99',
                opacity: 1.0,
                display: 'flex',
                alignItems: 'center',
                background: 'black',
                width: '30vw',
                height: 70,
                color: 'white',
                fontFamily: 'Lobster',
            },
            logo: {
                margin: '0 auto',
            },
            body: {
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                width: '100vw',
                height: '100vh',
                filter: this.state.menuOpen ? 'blur(2px)' : null,
                transition: 'filter 0.5s ease',
            },
        }
        const menu = ['Home', 'About', 'Experience', 'Contact']
        const menuItems = menu.map((val, index) => {
            return (
                <MenuItem
                    key={index}
                    delay={`${index * 0.1}s`}
                    onClick={() => { this.handleLinkClick(val); }}>{val}</MenuItem>)
        });

        return (
            <div style={{ backgroundColor: 'black', display: 'flex', justifyContent: 'flex-start', height: window.innerHeight }}>
                {/* <img
                    src={"https://images.pexels.com/photos/733475/pexels-photo-733475.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"}
                    style={{ width: '100%', height: '100%', objectFit: 'cover' }}
                /> */}
                <div style={styles.container}>
                    <MenuButton open={this.state.menuOpen} onClick={() => this.handleMenuClick()} color='#FF0076' />
                    {/* <div style={styles.logo}>Logo</div> */}
                </div>
                <Menu open={this.state.menuOpen}>
                    {menuItems}
                </Menu>
                {/* <div style={styles.body}>
                    <Footer name='Menu' />
                </div> */}
            </div>
        )
    }
}

/* MenuItem.jsx*/
class MenuItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            hover: false,
        }
    }

    handleHover() {
        this.setState({ hover: !this.state.hover });
    }

    render() {
        const styles = {
            container: {
                opacity: 0,
                animation: '1s appear forwards',
                animationDelay: this.props.delay,
            },
            menuItem: {
                fontFamily: `'Open Sans', sans-serif`,
                fontSize: '1.2rem',
                padding: '1rem 0',
                margin: '0 5%',
                fontStyle: 'bold',
                cursor: 'pointer',
                color: this.state.hover ? '#FF0076' : '#fafafa',
                marginLeft: this.state.hover ? 20 : '5%',
                transition: 'color 0.2s ease-in-out',
                animation: '0.5s slideIn forwards',
                animationDelay: this.props.delay
            },
            line: {
                width: '90%',
                height: '1px',
                background: 'invisible',
                margin: '0 auto',
                animation: '0.5s shrink forwards',
                animationDelay: this.props.delay,

            }
        }
        return (
            <div style={styles.container}>
                <div
                    style={styles.menuItem}
                    // onMouseEnter={() => { this.handleHover(); }}
                    // onMouseLeave={() => { this.handleHover(); }}
                    onClick={this.props.onClick}
                >
                    {this.props.children}
                </div>
                <div style={styles.line} />
            </div>
        )
    }
}

/* Menu.jsx */
class Menu extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: this.props.open ? this.props.open : false,
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.open !== this.state.open) {
            this.setState({ open: nextProps.open });
        }
    }

    render() {
        const styles = {
            container: {
                position: 'absolute',
                top: 0,
                left: '16vw',
                height: this.state.open ? '26em' : 0,
                width: '30vw',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                background: 'black',
                opacity: 1.0,
                color: '#fafafa',
                transition: 'height 0.8s ease',
                zIndex: 2,
                backgroundColor: 'black',
                paddingLeft: 10,
                paddingRight: 20,
                paddingBottom: 0,
            },
            menuList: {
                paddingTop: '3rem',
                width: '100%',
            }
        }
        return (
            <div style={styles.container}>
                {
                    this.state.open ?
                        <div style={styles.menuList} className='disable_text_highlighting'>
                            {this.props.children}
                        </div> : null
                }
            </div>
        )
    }
}

/* MenuButton.jsx */
class MenuButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: this.props.open ? this.props.open : false,
            color: this.props.color ? this.props.color : 'black',
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.open !== this.state.open) {
            this.setState({ open: nextProps.open });
        }
    }

    handleClick() {
        this.setState({ open: !this.state.open });
    }

    render() {
        const styles = {
            container: {
                height: '64px',
                width: '64px',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                cursor: 'pointer',
            },
            line: {
                height: '2px',
                width: '22px',
                background: this.state.color,
                transition: 'all 0.2s ease',
            },
            lineTop: {
                transform: this.state.open ? 'rotate(45deg)' : 'none',
                transformOrigin: 'top left',
                marginBottom: '5px',
                display: 'flex',
                position: 'absolute',
                left: 20,
                top: 30,
            },
            lineMiddle: {
                opacity: this.state.open ? 0 : 1,
                transform: this.state.open ? 'translateX(-16px)' : 'none',
                display: 'flex',
                position: 'absolute',
                left: 20,
                top: 38,
            },
            lineBottom: {
                transform: this.state.open ? 'translateX(-1px) rotate(-45deg)' : 'none',
                transformOrigin: 'top left',
                marginTop: '5px',
                display: 'flex',
                position: 'absolute',
                left: 20,
                top: 40,
            },
        }
        return (
            <div style={styles.container}
                onClick={this.props.onClick ? this.props.onClick :
                    () => { this.handleClick(); }}>
                <div style={{ ...styles.line, ...styles.lineTop }} />
                <div style={{ ...styles.line, ...styles.lineMiddle }} />
                <div style={{ ...styles.line, ...styles.lineBottom }} />
            </div>
        )
    }
}

