const MediaQueryDevice = (props) => {
    const [fullScreen, setFullScreen] = useState(false);

    return (
        <div style={fullScreen ? { position: 'fixed', left: 0, top: 0, zIndex: 150, backgroundColor: '#000000C0', width: window.innerWidth, height: window.innerHeight } : {}}>

            {fullScreen ?
                <div>
                    <div
                        className="interactivediv"
                        // onKeyDown={(e) => {
                        //     setFullScreen(!fullScreen);
                        //     props.setFullScreen(!fullScreen);
                        //     document.body.style.overflow = !fullScreen ? 'hidden' : 'unset';
                        // }}
                        style={fullScreen ? { display: 'flex', textAlign: 'center', justifyContent: 'center', alignItems: 'center', position: 'fixed', right: '20%', top: 0, backgroundColor: fullScreen ? 'black' : 'red', color: 'white', width: 100, height: 50, fontSize: '1.2rem' } : { display: 'flex', textAlign: 'center', justifyContent: 'center', alignItems: 'center', backgroundColor: 'red', color: 'white', width: 100, height: 50 }}
                        onClick={() => {
                            setFullScreen(!fullScreen);
                            props.setFullScreen(!fullScreen);
                            document.body.style.overflow = !fullScreen ? 'hidden' : 'unset';
                        }}
                    >
                        {!fullScreen ? "Full Screen" : "Close"}
                    </div>
                    <div style={{ marginLeft: '5vw' }}>
                        <Device url={props.url} turnon width={window.innerWidth - 50} height={window.innerHeight - 50} />
                    </div>
                </div> :
                <div>
                    <MediaQuery minDeviceWidth={200} maxWidth={500} >

                        <div
                            className="interactivediv"
                            style={fullScreen ? { display: 'flex', textAlign: 'center', justifyContent: 'center', alignItems: 'center', position: 'fixed', right: '20%', top: 20, backgroundColor: 'black', color: 'white', width: 100, height: 50 } : { display: 'flex', textAlign: 'center', justifyContent: 'center', alignItems: 'center', backgroundColor: 'indigo', color: 'white', width: 100, height: 50 }}
                            onClick={() => {
                                // alert("Press");
                                setFullScreen(!fullScreen);
                                props.setFullScreen(!fullScreen);
                                document.body.style.overflow = !fullScreen ? 'hidden' : 'unset';
                            }}>
                            {!fullScreen ? "Full Screen" : "Close"}
                        </div>
                        {fullScreen ? <Device turnon url={props.url} width={window.innerWidth - 50} height={window.innerHeight - 50} /> : <Device turnon url={props.url} width={200} height={400} />
                        }
                        {/* <Device width={window.innerWidth * 7 / 9} height={window.innerWidth + 150} /> */}
                        {/* <div style={{ backgroundColor: 'red' }}>Mobile</div> */}
                    </MediaQuery>
                    <MediaQuery minDeviceWidth={501}>
                        <Device turnon url={props.url} />
                        {/* <div>Desktop</div> */}
                    </MediaQuery>
                    {/* <MediaQuery minResolution='2dppx'>
                {(matches) =>
                    matches
                        ? <div>
                            You are retina
                                        </div>
                        : <div>You are not retina</div>
                }
            </MediaQuery> */}
                </div>
            }

        </div >
    )
}

